@import url(https://fonts.googleapis.com/css?family=Pacifico);
body {
  margin: 0;
  font-family: 
    -apple-system, 
    BlinkMacSystemFont, 
    'Segoe UI', 
    'Roboto', 
    'Oxygen',
    'Ubuntu', 
    'Cantarell', 
    'Fira Sans', 
    'Droid Sans', 
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 85vh;
  overflow: hidden;
  background: linear-gradient(#e66465, #9198e5);
}

html, 
body {
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  touch-action: manipulation;
}

h2 {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

h3 {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}

h5 {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  -webkit-margin-after: 0em;
          margin-block-end: 0em;
}

.matchTitle {
  font: 400 30px/1.3 'Pacifico', Helvetica, sans-serif;
  color: #2b2b2b;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.31);
  margin-bottom: 20px;
}

.matchName {
  margin: 4px;
}

.main-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: auto;
  -webkit-animation: popUp 400ms;
          animation: popUp 400ms;
  padding: 20px;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 0 14px 0 #797986;
}

.main-page >div {
  width: 220px;
}

.popUp {
  -webkit-animation: fastPopUp 200ms;
          animation: fastPopUp 200ms;
}

@-webkit-keyframes fastPopUp {
  0% {-webkit-transform: scale(0.5);transform: scale(0.5)} 
  100% {-webkit-transform: scale(1);transform: scale(1)}
}

@keyframes fastPopUp {
  0% {-webkit-transform: scale(0.5);transform: scale(0.5)} 
  100% {-webkit-transform: scale(1);transform: scale(1)}
}


@-webkit-keyframes popUp {
  0% {-webkit-transform: scale(0);transform: scale(0)} 
  70% {-webkit-transform: scale(1.1);transform: scale(1.1)} 
  85% {-webkit-transform: scale(0.94);transform: scale(0.94)} 
  100% {-webkit-transform: scale(1);transform: scale(1)}
}


@keyframes popUp {
  0% {-webkit-transform: scale(0);transform: scale(0)} 
  70% {-webkit-transform: scale(1.1);transform: scale(1.1)} 
  85% {-webkit-transform: scale(0.94);transform: scale(0.94)} 
  100% {-webkit-transform: scale(1);transform: scale(1)}
}

input[type=text] {
    width: 200px;
    border:0px;
    border-radius:8px;
    margin-top: 5px;
    margin-bottom: 5px;
    outline: none;
    padding: 17px;
    box-sizing:border-box;
    transition:.3s;
    background-color: rgba(0, 0, 0, 0.13);
    text-align: center;
    font-size: medium;
  }
  
input[type=text]:focus {
  border-color:#9198e5;
  box-shadow: 0 0 8px 0 #9198e5;
  -webkit-appearance: none;
  -webkit-box-shadow: 0 0 8px 0 #9198e5;
}

button {
  background-color: #9198e5;
  font-family: Arial, Helvetica, sans-serif;
  font-size: large;
  color: white;
  padding: 15px;
  width: 200px;
  text-align: center;
  text-decoration: none;
  overflow: hidden;
  border-radius: 8px;
  margin-bottom:5px;
  margin-top: 5px;
  box-shadow: 0 3px rgba(153, 153, 153, 0.24);
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  border: 0;
}

button:active {
  background-color: rgba(128, 138, 245, 0.555);
  box-shadow: 0 0px rgba(153, 153, 153, 0.24);
  -webkit-transform: translateY(2.5px);
          transform: translateY(2.5px);
  transition: all 0.2s;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  border: 0;
}

button:disabled{
  background-color: rgba(196, 196, 196, 0.514);
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  box-shadow: none;
}

.logo {
  padding-top: 50px;
  padding-bottom: 50px;
  height: 47px;
  width: 200px;
}

._loading_overlay_overlay {
  border-radius: 15px;
}

.restaurantName {
  padding: 5px;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.356);
  border-radius: 10px;
  color: white;
  display: inline-block;
  left: 0;
  pointer-events: none;
}

.restaurantRatingPrice {
  padding: 5px;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.356);
  border-radius: 10px;
  color: white;
  display: inline-block;
  position: absolute;
  left: 6px;
  bottom: 0;
  pointer-events: none;
}

.restaurantDistance {
  padding: 5px;
  margin: 10px;
  background-color: rgba(0, 0, 0, 0.356);
  border-radius: 10px;
  color: white;
  display: inline-block;
  position: absolute;
  right: 6px;
  bottom: 0;
  pointer-events: none;
}
#root {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  /* background: linear-gradient(#fff, #999); */
  background: linear-gradient(#e66465, #9198e5);
}

#root>div {
  width: 260px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1 {
  font-family: 'Alatsi', sans-serif;
  /* font-family: 'Damion', cursive; */
  /* color: #F38D68; */
  color: #fff;
  text-shadow: 0px 0px 60px 0px rgba(0,0,0,0.30);
}

