  padding: 5px;
  background-color: rgba(255, 255, 255, 0.397);
} */

.swipe {
  position: absolute;
}

.cardContainer {
  width: 90vw;
  max-width: 260px;
  height: 300px;
}

.card {
  position: relative;
  background-color: #fff;
  width: 80vw;
  max-width: 260px;
  height: 300px;
  /* box-shadow: 0px 0px 60px 0px rgba(0,0,0,0.30); */
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.card h3 {
  position: absolute;
  bottom: 0;
  margin: 10px;
  color: #fff;
}

.infoText {
  width: 100%;
  height: 28px;
  justify-content: center;
  display: flex;
  /* color: #F38D68; */
  color: #fff;
  -webkit-animation: popup 200ms;
          animation: popup 200ms;
}

@-webkit-keyframes popup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  } 70% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  } 85% {
    -webkit-transform: scale(0.94);
            transform: scale(0.94);
  } 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes popup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  } 70% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  } 85% {
    -webkit-transform: scale(0.94);
            transform: scale(0.94);
  } 100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.container {
  width: 100%;
  height: calc(100vh - 16px);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: opacity 0.1s ease-in-out;
}

.container .cards_container {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.container .cards_container .card_container {
  display: inline-block;
  overflow: hidden;
  background-color: #fff;
  width: 80vw;
  max-width: 260px;
  height: 300px;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  position: absolute;
  will-change: transform;
  transition: all 0.3s ease-in-out;
  cursor: -webkit-grab;
  cursor: grab;
}

.cards_container .end_card {
  -webkit-animation: 2s delayAppear;
          animation: 2s delayAppear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  visibility: hidden;
  display: inline-block;
  overflow: hidden;
  background-color: #fff;
  width: 80vw;
  max-width: 260px;
  height: 300px;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  position: absolute;
  will-change: transform;
  transition: all 0.3s ease-in-out;
}

@-webkit-keyframes delayAppear {
  99% {
    visibility: hidden;
  }
  100% {
    visibility: visible;
  }
}

@keyframes delayAppear {
  99% {
    visibility: hidden;
  }
  100% {
    visibility: visible;
  }
}

.container .cards_container .moving.card_container {
  transition: none;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

